import Vue from "vue";
import Vuex from "vuex";

import { Character, Pagination } from "@/interfaces";

Vue.use(Vuex);

export default new Vuex.Store({
	state: {
		currentCharacter: {} as Character,
		characters: [] as Character[],
		filterCharacters: [] as Character[], // Only used from searchbar
		keepCharacters: [] as Character[],
		pagination: {} as Pagination,
		loading: {
			characters: true,
			characterDetail: true,
		},
	},
	mutations: {
		setLoading(state, data) {
			const { loadingKey, value } = data;

			if (!value) {
				setTimeout(() => {
					(state as any).loading[loadingKey] = false;
				}, 1500);
			} else {
				(state as any).loading[loadingKey] = value;
			}
		},
		setCharacters(state, data) {
			state.pagination = data.info;
			state.characters = data.results;
			state.keepCharacters = data.results; // Keep one version for reset
		},
		filterCharacterByName(state, name: string) {
			const characters = state.keepCharacters.filter((c) => c.name.toLowerCase().includes(name.toLowerCase()));

			if (name === "") {
				state.characters = state.keepCharacters;
				state.filterCharacters = [];

				return;
			}
			if (characters.length > 0) {
				state.characters = characters;
				state.filterCharacters = state.characters;
			} else {
				state.filterCharacters = [];
			}
		},
		setPage(state, currentPage: number) {
			state.pagination.currentPage = currentPage;
		},
	},
	getters: {
		characters: (state) => state.characters,
		filterCharacters: (state) => state.filterCharacters,
	},
	actions: {},
	modules: {},
});
